html {
  box-sizing: border-box;
}

*,
 *::before,
 *::after {
     padding: 0;
     margin: 0;
     box-sizing: inherit;
 }

 
body {
  margin: 0;
  font-family: 'Poppins', sans-serif;
  background-image: linear-gradient(45deg, #242423e1, #1b1b1ae1), url(https://images.pexels.com/photos/267507/pexels-photo-267507.jpeg?auto=compress&cs=tinysrgb&dpr=1&w=500);
  background-position: center;
  background-size: cover;
  background-repeat: no-repeat;
  height: 100vh;
}

